import React from 'react'
import styled from 'styled-components';
import { Row, Col } from 'antd';

const FirstTitle = styled.h1`
font-size: ${props => props.size};
color: ${({ theme }) => theme.colors.white};
text-align: ${props => props.align};
text-transform: uppercase;
font-family: 'Arial Narrow';
padding-top: ${props => props.top};
margin-bottom: 0px;
`
const SecondTitle = styled.h2`
font-size: 3vw;
color: ${({ theme }) => theme.colors.white};
text-align: left;
text-transform:uppercase;
font-family: 'Arial Narrow';
margin-bottom: 1%;
span {
    font-size: 2.5vw;
}
`
const Text1 = styled.p`
font-size: 1.8vw;
color: ${({ theme }) => theme.colors.white};
text-align: left;
text-transform: initial;
font-family: 'Helvetica';
padding-top: 4%;
`
const Text2 = styled.p`
font-size: 1.4vw;
color: ${({ theme }) => theme.colors.purpleblack};
line-height: 1.7;
text-align: justify;
text-transform: initial;
font-family: 'Helvetica';
padding-top: 15%;
padding-left: 10%;
`
const Titles =styled.div`
width: 60%;
font-size: 3vw;
color: ${({ theme }) => theme.colors.white};
background: ${({ theme }) => theme.colors.backtitles};
text-align: center;
text-transform: uppercase;
font-family: 'Arial Narrow';
border-top-right-radius: 5px;
border-bottom-right-radius: 5px;
margin-top: 5%;
`
const Palavraazul =styled.p`
font-size: ${props => props.size};
color: ${({ theme }) => theme.colors.lightblue};
text-align: left;
text-transform: uppercase;
font-family: 'Courier';
padding-top: 12.5%;
padding-left: ${props => props.left};
margin:0;
line-height: 0.8;
`
const Palavraroxa =styled.p`
font-size: ${props => props.size};
color: ${({ theme }) => theme.colors.purple};
text-align: left;
text-transform: uppercase;
font-family: 'Courier';
padding-left: ${props => props.left};
margin:0;
line-height: 1;
`
const Palavraamarela =styled.p`
font-size: ${props => props.size};
color: ${({ theme }) => theme.colors.yellow};
text-align: left;
text-transform: uppercase;
font-family: 'Courier';
padding-left: ${props => props.left};
margin:0;
line-height: 1;
`
const Palavrarosa =styled.p`
font-size: ${props => props.size};
color: ${({ theme }) => theme.colors.pink};
text-align: left;
text-transform: uppercase;
font-family: 'Courier';
padding-left: ${props => props.left};
margin:0;
line-height: 1;
`

export function Firstsectiontext(props) {
    return (
        <div>
            <FirstTitle align="left" top="5%" size="4.5vw">{props.firsttitle}</FirstTitle>
            <SecondTitle><span>by:</span>{props.secondtitle}</SecondTitle>
        </div>
    )
}

export function Subtitles(props) {
    return (
        <div>
             <FirstTitle align="center" top="1.5%" size="4vw">{props.firsttitle}</FirstTitle>
        </div>
    )
}

export function Textperfil(props) {
    return (
        <div>
            <Text1>{props.textp1}<br />{props.textp2}</Text1>
        </div>
    )
}


export function Texto(props) {
    return (
        <div>
            <Text2>{props.textp1}<br />{props.textp2}</Text2>
        </div>
    )
}

export function Texttitles(props) {
    return (
        <div>
            <Titles>{props.title}</Titles>
        </div>
    )
}

export function PalavrasColoridas(props) {
    return (
        <div>
            <Palavraazul size="5vw" left="10%">{props.word1}</Palavraazul><br/>
            <Palavraroxa size="5.3vw" left="25%">{props.word2}</Palavraroxa><br/>
            <Palavraamarela size="5.8vw" left="10%">{props.word3}</Palavraamarela><br />
            <Palavrarosa size="6.2vw" left="25%">{props.word4}</Palavrarosa>
        </div>
    )
}

export function PalavraAzul(props) {
    return (
        <div>
            <Palavraazul size="3vw" left="10%">{props.word1}</Palavraazul><br/>
        </div>
    )
}